.header {
	position: sticky;
	top: -6.5rem;
	left: 0;
	z-index: 31;
	box-sizing: border-box;
	width: 100%;
	background-color: #fff;
}

.header-top {
	box-sizing: border-box;
	width: 100%;
	height: 6.5rem;
	background-color: #fff;
	transition: all .5s ease .01ms;
	font-size: 1.7rem;
}

.header-top h1 {
	text-align: center;
}

.header-top h1 a {
	display: inline-block;
}

.header-top h1 img {
	height: 37px;
	transition: height .5s ease 0.01ms;
}

.header-top .btn-drawer {
	width: 3.4rem;
	height: 3.4rem;
	border-radius: .7rem;
	background-color: #DCDFE4;
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 1.5rem;
	right: 1.7rem;
}

.header-top.sub {
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #EFF2F7;
}

.header-top.sub h1 {
	/*font-family:"ManropeB", "NotoM", serif;*/
	font-family: "SpoqaB", serif;
	font-weight: normal;
	padding: 0 6rem;
	word-break: break-all;
	flex-shrink: 1;
	font-size: 1.4rem;
}

.header-top.sub .btn-prev {
	width: 3.4rem;
	height: 3.4rem;
	border-radius: .7rem;
	background-color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 1.5rem;
	left: 1.7rem;
}

.header-nav {
	scrollbar-width: none;
	-ms-overflow-style: none;
	box-sizing: border-box;
	font-size: 0;
	overflow-x: visible;
	overflow-y: hidden;
	white-space: nowrap;
	-webkit-overflow-scrolling: touch;
	width: 100%;
	padding-bottom: .2rem;
	padding-left: 1.7rem;
	font-size: 2.4rem;
	color: #000;
	letter-spacing: 0;
}

.header-nav::-webkit-scrollbar {
	display: none;
}

.header-nav a {
	position: relative;
	display: inline-block;
	padding: 1rem 0.5rem 0.8rem 0;
	color: #CBCED2;
	line-height: 1;
}

.header-nav a.active {
	color: #2B2B2B;
}

.header-nav a.active:after {
	display: block;
	content: '';
	position: absolute;
	bottom: 0;
	left: 50%;
	z-index: 1;
	width: .5rem;
	height: .5rem;
	margin-left: -.2rem;
	border-radius: 50%;
	background-color: #F04040;
}

.footer {
	margin-top: auto;
	padding-top: 65px;
	background-color: #f2f2f2;
}

.footer .inner {
	max-width: 1300px;
	margin: 0 auto;
}

.footer .link-list {
	position: relative;
	display: inline-block;
}

.footer .link-list .selectbox {
	font-size: 13px;
	display: inline-block;
	width: 215px;
	vertical-align: middle;
	overflow: visible;
	border: 0;
}

.footer .link-list .label {
	text-align: left;
	box-sizing: border-box;
	display: block;
	width: 100%;
	height: 100%;
	padding-right: 40px;
	padding-left: 20px;
	border: 1px solid #ccc;
}

.footer .link-list ul {
	display: none;
}

.footer .link-list.active ul {
	display: block;
	position: absolute;
	top: 100%;
	left: 0;
	z-index: 10;
	box-sizing: border-box;
	width: 100%;
	margin-top: -1px;
	border: 1px solid #ccc;
	background-color: #fff;
}

.footer .link-list.active ul a {
	display: block;
	height: 100%;
	padding-right: 40px;
	padding-left: 20px;
}

.footer .link-list.active ul a:hover {
	background-color: #f4f4f4;
}

.footer-menu-list {
	display: table;
	width: 100%;
	max-width: 1300px;
	margin: 0 auto 65px;
}

.footer-menu-list:after {
	content: '';
	display: block;
	clear: both;
}

.footer-menu-list .menu-item {
	display: table-cell;
	width: 25%;
	padding-left: 20px;
	box-sizing: border-box;
}

.footer-menu-list .menu-item strong {
	display: block;
	margin-bottom: 15px;
	font-size: 18px;
	font-weight: 700;
	line-height: 1.5;
}

.footer-menu-list .menu-item li a {
	color: #0061a0;
	font-size: 15px;
	line-height: 1.8;
}

.footer-site {
	position: relative;
	margin-bottom: 40px;
}

.footer-site .sns {
	position: absolute;
	bottom: 0;
	right: 50px;
}

.footer-site .sns a {
	display: inline-block;
	margin-left: 15px;
	font: 0/0 a;
}

.footer-info {
	padding-bottom: 75px;
	border-top: 1px solid #ccc;
}

.footer-info:after {
	content: '';
	display: block;
	clear: both;
}

.footer-logo {
	float: left;
	margin-top: 75px;
	margin-right: 68px;
}

.footer-etc {
	float: left;
	margin-top: 40px;
	font-size: 14px;
	line-height: 1.7;
}

.footer-etc .link-list .label {
	display: none;
}

.footer-etc .link-list ul {
	display: block;
}

.footer-etc .link-policy {
	margin-bottom: 13px;
}

.footer-etc .link-policy:after {
	content: '';
	display: block;
	clear: both;
}

.footer-etc .link-policy li {
	float: left;
	margin-right: 25px;
}

.footer-etc .link-policy li a {
	color: #0061a0;
	line-height: 2.1;
}

.footer-award {
	position: relative;
	float: right;
	margin-top: 67px;
}

.footer-award .icon-award {
	position: absolute;
	top: 50%;
	left: 0;
	margin-top: -26px;
}

.footer-award span {
	display: inline-block;
	padding-left: 49px;
	font-size: 12px;
	line-height: 1.5;
	color: #666;
}

.fixedBottom {
	display: flex;
	justify-content: space-between;
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 41;
	width: 100%;
	height: 50px;
	padding: 0 13px;
	font: 0/0 a;
	background-color: #222;
	box-sizing: border-box;
	transform: translate3d(0, 0, 0);
	transition: transform .2s;
}

.fixedBottom button {
	color: #fff;
	width: auto;
	font-size: 1rem;
}

.fixedBottom.close {
	transform: translate3d(0, 100%, 0);
}

.estimateBottom {
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 41;
	width: 100%;
	height: 4.7rem;
	font: 0/0 a;
	box-sizing: border-box;
	box-shadow: 0 -3px 12px rgba(96, 97, 112, 0.3);
}

.estimateBottom .bottom-in {
	display: flex;
	align-items: center;
	justify-content: center;
}

.estimateBottom .btn-white {
	font-size: 1.1rem;
	width: 25%;
}

.estimateBottom .btn-white i {
	margin-right: .4rem;
}

.estimateBottom .btn-white span {
	display: inline-block;
	vertical-align: middle;
}

.estimateBottom .btn-red {
	width: 75%;
}

.estimateBottom .ty2 .btn-white {
	font-size: 1.1rem;
	width: 25%;
	color: #2B2B2B;
}

.estimateBottom .ty2 .btn-orange {
	font-size: 1.1rem;
	width: 25%;
}

.estimateBottom .ty2 .btn-red {
	flex-flow: column;
	width: 50%;
}

.estimateBottom .ty2 .btn-red span {
	font-size: 1rem;
	margin-bottom: .4rem;
	/*font-family:"ManropeL", "NotoL", serif;*/
	font-family: "SpoqaL", serif;
	font-weight: normal;
}

.drawer {
	position: fixed;
	top: 0;
	right: 0;
	width: 100%;
	height: 100%;
	overflow-x: hidden;
	overflow-y: auto;
	z-index: 70;
	background-color: #fff;
	transform: translateX(100%);
	transition: transform .4s;
	background-repeat: no-repeat;
	background-image: url("../../resources-mo/images/icons/error.png");
	background-position: 0 0;
	background-size: auto;
}

.drawer.active {
	transform: translateX(0);
}

.drawer .drawer-top {
	position: sticky;
	top: 0;
	left: 0;
	z-index: 1;
}

.drawer .drawer-top .drawer-btns {
	height: 6.8rem;
	padding: 0 2.4rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.drawer .drawer-top .drawer-btns > div {
	display: flex;
	align-items: center;
	justify-content: center;
}

.drawer .drawer-top .drawer-btns .left button {
	margin-right: 1rem;
}

.drawer .drawer-top .drawer-btns .right button {
	margin-left: 1rem;
}

.drawer .drawer-top.logout-state {
	background-color: #fff;
}

.drawer .drawer-top.logout-state .logout-state-box {
	background-color: #EFF2F7;
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 4.1rem;
	padding: 0 1.7rem;
}

.drawer .drawer-top.logout-state .logout-state-box button {
	padding: 0 1.3rem;
}

.drawer .drawer-top.logout-state .login-state-box {
	display: none;
}

.drawer .drawer-top.login-state {
	background-color: #EFF2F7;
}

.drawer .drawer-top.login-state .login-state-box .login-top {
	background-color: #EFF2F7;
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 4.1rem;
	padding: 0 1.7rem;
}

.drawer .drawer-top.login-state .login-state-box .login-top strong {
	/*font-family:"ManropeB", "NotoM", serif;*/
	font-family: "SpoqaB", serif;
	font-weight: normal;
	font-size: 1.8rem;
}

.drawer .drawer-top.login-state .login-state-box .login-btns {
	padding: .8rem 1.7rem 1.7rem;
}

.drawer .drawer-top.login-state .login-state-box .login-btns button {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
}

.drawer .drawer-top.login-state .login-state-box .login-btns button b {
	/*font-family:"ManropeB", "NotoM", serif;*/
	font-family: "SpoqaB", serif;
	font-weight: normal;
	font-size: 1.4rem;
	color: #F04040;
}

.drawer .drawer-top.login-state .logout-state-box {
	display: none;
}

.drawer .drawer-list h5 {
	padding: 0 1.7rem;
	margin: 2.3rem 0 1.2rem;
}

.drawer .drawer-list h5 span {
	/*font-family:"ManropeB", "NotoM", serif;*/
	font-family: "SpoqaB", serif;
	font-weight: normal;
	display: inline-block;
	vertical-align: middle;
	font-size: 1.4rem;
	margin-left: .7rem;
}

.drawer .drawer-list h5 a {
	display: block;
}

.drawer .drawer-list h5 a:after {
	display: inline-block;
	vertical-align: middle;
	background-repeat: no-repeat;
	background-image: url("../../resources-mo/images/icons/ico-next.png");
	background-position: center center;
	background-size: 100% auto;
	content: '';
	width: .6rem;
	height: 1.2rem;
	transition: transform .5s;
	margin-left: .4rem;
}

.drawer .drawer-list > ul {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	border-bottom: 1px solid #EBEBEB;
}

.drawer .drawer-list > ul > li {
	box-sizing: border-box;
	border-top: 1px solid #EBEBEB;
	width: 50%;
}

.drawer .drawer-list > ul > li a {
	padding-left: 1.7rem;
	display: flex;
	align-items: center;
	height: 4rem;
}

.drawer .drawer-list > ul:after {
	display: block;
	content: '';
	background-color: #EBEBEB;
	width: 1px;
	height: 100%;
	position: absolute;
	top: 0;
	left: 50%;
	margin-left: -.5px;
}
